import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ProductRegistrationService } from '../product-registration.service';
import { Router } from '@angular/router';
import { Product } from '../product';

@Component({
  selector: 'app-productregister',
  templateUrl: './productregister.component.html',
  styleUrls: ['./productregister.component.css']
})
export class ProductregisterComponent implements OnInit {

  constructor(private service: ProductRegistrationService,private router: Router) { }
  product: Product = new Product("","",0,"");
  message: any;
  ngOnInit() {
  }
  onAddPost(form:NgForm){
    if(form.invalid){
      console.log("Invalid");
      return;
    }
    console.log(form.valid);
    
  }
  // public registerProcess(){
  //   let response = this.service.doProductRegistration(this.product);
  //   response.subscribe((data) => this.message = (data));
  // }

  public async registerProcess(){
    let response = await this.service.doProductRegistration(this.product);
    response.subscribe((data) => this.message = (data));
    this.router.navigateByUrl("/sellproduct");
  }

}
